
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import {httpUrl} from '../../http_url';
var a=2;
export default {
directives: {
TransferDom
},
data(){
return{
dataList:[],
addMOre:true,
page:1,
length:'',
dataListLength:''
}
},

created(){

this.getDataList()
},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton,
Alert,
Toast,
Divider,
Icon,
XTable
},
methods:{
getDataList(){
const _this = this;
let postData = this.$qs.stringify({
page:this.page,
})
this.$axios({
method:'post',
url:httpUrl.getMoneyLog,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData,
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.length = res.data.retData.length;
this.dataList = res.data.retData;
this.dataList = res.data.retData;
this.dataListLength = this.dataList.length;

}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})

}

}).catch(function(){
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
})
},
showMore(){
const _this = this;

let postData = this.$qs.stringify({
page:++this.page,
})

this.$axios({
method:'post',
url:httpUrl.getMoneyLog,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{

console.log(res.data)
if(res.data.errCode==200){
this.dataList.concat(res.data['retData']);
console.log(this.dataList)
console.log(this.page);

console.log(res.data.retData)

this.length = res.data.retData.length;

if(this.length == 0){
this.$vux.toast.show({
text: '暂无更多数据',
type:'warn'
})
this.addMOre = false;
}
const data = res.data.retData;
const a = this.dataList.concat(data)
this.dataList = a;
this.dataListLength = this.dataList.length;

}
}).catch(function(){
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
})
} ,
}
}




